import React, { useState } from "react";

const Star = ({ starId, rating, onMouseEnter, onMouseLeave, onClick }) => {
  let styleClass = "star-blank";
  if (rating && rating >= starId) {
    styleClass = "star-filled";
  }
  return (
    <React.Fragment>
      <svg
        className={styleClass}
        height="55px"
        width="53px"
        data-rating="1"
        onMouseEnter={onMouseEnter}
        onMouseLeave={onMouseLeave}
        onClick={onClick}
      >
        <polygon
          stroke-width="0"
          points="9.9, 1.1, 3.3, 21.78, 19.8, 8.58, 0, 8.58, 16.5, 21.78"
        />
      </svg>
    </React.Fragment>
  );
};

function Ratings() {
  const starCount = [1, 2, 3, 4, 5];
  const [rating, setRating] = useState(0);
  const [hoverRating, setHoverRating] = useState(0);

  return (
    <div className="container">
      <div className="row">
        <div className="column">
          <div className="showRatings">
            <h3 className="text-center"> Star Ratings:</h3>

            <div className="ratingsHolder" id="ratingsHolder">
              {/* MAP ONLY WORKS FOR ARRAY */}

              {starCount.map((star, i) => (
                <Star
                  key={i}
                  starId={i}
                  rating={hoverRating || rating}
                  onMouseEnter={() => setHoverRating(i)}
                  onMouseLeave={() => setHoverRating(0)}
                  onClick={() => setRating(i)}
                />
              ))}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Ratings;
